<template>
    <yd-layout class="box_danger_correct">
        <navBar title="隐患整改" slot="navbar"></navBar>
        <div class="box_form">
            <div class="box_cont" style="align-items: center;">
                <p class="mright">金额：</p>
                <input v-on:focus="heighOn" type="number" class="box_input" placeholder="请输入整改金额(元)" v-model="money">
            </div>
            <div class="box_cont mtop">
                <p class="mright">备注：</p>
                <textarea v-on:focus="heighOn" rows="5" class="box_txt" placeholder="请输入备注" v-model="remark"></textarea>
            </div>
            <div class="box_cont mtop">
                <p class="mright">附件：</p>
                <unphoto v-on:imagesUrl="saveUrl"></unphoto>
            </div>
            <div class="box_cont mtop">
                <p class="mright">复查：</p>
                <yd-radio-group v-model="recheck" color="#1D91E6">
                    <yd-radio val="0">需要复查</yd-radio>
                    <yd-radio val="1">不需要复查</yd-radio>
                </yd-radio-group>
            </div>
            <yd-button @click.native="saveData" size="large" color="#fff" bgcolor="#1D91E6">保存</yd-button>
        </div>
    </yd-layout>
</template>

<script>
    import {api_xunjian_correct} from '../../components/APIKeys'
    import unphoto from '../../components/unphoto.vue'

    export default {
        name: "DangerCorrect",
        components:{unphoto},
        data(){
            return {
                token:window.localStorage.getItem('token'),
                danger:JSON.parse(window.localStorage.getItem('danger')),
                money:null,//整改金额
                remark:'',//整改备注
                photoUrl:'',//附件
                recheck:0,//是否需要复查
            }
        },
        methods:{
            //保存图片的base64
            saveUrl:function(data){
                this.photoUrl = data;
            },
            //点击保存
            saveData(){
                if(this.money == '' || this.money == null || this.money.length < 1){
                    this.$showError('请输入整改金额');
                    return false;
                }else if(this.remark == ''){
                    this.$showError('请输入整改备注');
                    return false;
                }else if(this.photoUrl.length < 1){
                    this.$showError('请上传整改图片');
                    return;
                }
                this.$showDialog();
                let jsonData = {
                    token:this.token,
                    yhjl_id:this.danger.yh_id,
                    yhzgje:this.money,
                    yhzgbz:this.remark,
                    yhzgzp:this.photoUrl,
                    state:this.recheck,
                }
                this.$post(api_xunjian_correct, jsonData).then(res =>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.$showSuccess('保存成功');
                        window.history.go(-2);
                    }else{
                        this.$showError('保存失败');
                    }
                })
            },
            //防止键盘遮挡
            heighOn: function() {
                if (/Android/gi.test(navigator.userAgent)) {
                    window.addEventListener('resize', function () {
                        if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                            window.setTimeout(function () {
                                document.activeElement.scrollIntoViewIfNeeded();
                            }, 0);
                        }
                    })
                }
            },
        }
    }
</script>

<style scoped>
    .box_danger_correct{
        background: #fff;
    }
    .box_form{
        padding: .3rem;
    }
    .box_cont{
        display: flex;
        /*align-items: center;*/
    }
    .box_input{
        height: 0.8rem;
        flex: 1;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        border-radius:5px;
    }
    .box_txt{
        flex: 1;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        padding: .2rem;
    }
    .mtop{
        margin-top: .2rem;
    }
    .mright{
        margin-right: .2rem;
        white-space:nowrap;
    }
    .box_light{
        flex: 1;
        display: flex;
        align-items: center;
        flex-wrap:wrap;
    }
    .box_img{
        width: calc(95% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        flex-direction:column;
        align-items: center;
        text-align: center;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        margin-bottom: .1rem;

    }
    .box_img img{
        width: 60%;
        height:60%;
    }
    .box_photo{
        width: calc(93% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        margin-bottom: .1rem;
        position: relative;
    }
    .box_photo .img1{
        width: 100%;
        height: 100%;
    }
    .box_delete{
        position: absolute;
        right: 0;
        top: 0;
        width: 35%;
        height: 35%;
    }
</style>
<style>
    .box_danger_correct .yd-radio>input[type=radio]:checked+.yd-radio-icon{
        margin-bottom: .05rem;
    }
    .box_danger_correct .yd-radio-icon{
        margin-bottom: .05rem;
    }
    .box_danger_correct .yd-radio{
        padding-right: 1rem;
    }
</style>



